@import url(https://fonts.googleapis.com/css?family=Montserrat:400,600&display=swap);
body{margin:0;font-family:"Monteserrat", sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale}code{font-family:source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace}*{box-sizing:border-box}

.app-container{background-color:#3b3b98;width:100%;height:100vh;overflow:hidden;display:-webkit-flex;display:flex;-webkit-align-items:center;align-items:center;-webkit-justify-content:center;justify-content:center;-webkit-flex-direction:column;flex-direction:column}.page-title{font-weight:bold;margin:20px 0 10px 0;color:#fff;text-transform:uppercase;text-align:center}

.cup{background-color:#fff;border:4px solid #144fc6;color:#144fc6;border-radius:0 0 40px 40px;height:330px;width:150px;margin:30px 0;display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;overflow:hidden}.remainded{display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;-webkit-align-items:center;align-items:center;-webkit-justify-content:center;justify-content:center;text-align:center;-webkit-flex:1 1;flex:1 1;transition:height 0.3s ease}.remainded span{font-size:32px;font-weight:bold}.remainded small{font-size:20px}.percentage{height:0px;background-color:#6ab3f8;font-size:30px;font-weight:bold;visibility:hidden;transition:height 0.3s ease;padding:15px;margin:0;width:100%}

.cup-small{display:-webkit-flex;display:flex;-webkit-align-items:center;align-items:center;-webkit-justify-content:center;justify-content:center;margin:10px 8px;width:60px;height:95px;background-color:rgba(255,255,255,0.9);border:4px solid #144fc6;border-radius:0 0 25px 25px;cursor:pointer;font-size:16px;font-weight:bold;text-align:center;color:#144fc6;transition:background-color 0.3s ease;overflow:hidden}.cup-small.full{background-color:#6ab3f8;color:#fff}.cup-small:active{-webkit-transform:scale(0.88);transform:scale(0.88)}

.cups{display:-webkit-flex;display:flex;-webkit-flex-wrap:wrap;flex-wrap:wrap;width:350px;-webkit-align-items:center;align-items:center;-webkit-justify-content:center;justify-content:center}

.paragraph{color:#fff;text-align:center;margin:0 0 20px}

